import { FilesetResolver, PoseLandmarker } from '@mediapipe/tasks-vision';
import {
  useCallback,
  useEffect,
  useRef,
  useState,
  type RefObject,
} from 'react';
import Webcam from 'react-webcam';

interface WebcamRef {
  video?: HTMLVideoElement | null;
}

interface PoseDetectionProps {
  videoRef: RefObject<Webcam | WebcamRef | null>; // Webcam  ref
  onPoseDetected?: (
    landmarks: PoseLandmark[],
    worldLandmarks?: PoseLandmark[],
  ) => void;
  isEnabled?: boolean;
}

interface PoseLandmark {
  x: number;
  y: number;
  z: number;
  visibility?: number;
}

const PoseDetection = ({
  videoRef,
  onPoseDetected,
  isEnabled = true,
}: PoseDetectionProps) => {
  const [isInitialized, setIsInitialized] = useState(false);
  const [isDetecting, setIsDetecting] = useState(false);
  const poseLandmarkerRef = useRef<PoseLandmarker | null>(null);
  const lastVideoTimeRef = useRef<number>(-1);

  // MediaPipe 
  useEffect(() => {
    const initializePoseLandmarker = async () => {
      try {
        const vision = await FilesetResolver.forVisionTasks(
          'https://cdn.jsdelivr.net/npm/@mediapipe/tasks-vision@0.10.0/wasm',
        );

        poseLandmarkerRef.current = await PoseLandmarker.createFromOptions(
          vision,
          {
            baseOptions: {
              modelAssetPath:
                'https://storage.googleapis.com/mediapipe-models/pose_landmarker/pose_landmarker_heavy/float16/1/pose_landmarker_heavy.task',
              delegate: 'GPU',
            },
            runningMode: 'VIDEO',
            numPoses: 1,
            minPoseDetectionConfidence: 0.2, //    
            minPosePresenceConfidence: 0.2, //    
            minTrackingConfidence: 0.2, //    
          },
        );

        setIsInitialized(true);
        console.log('Pose landmarker initialized successfully');
      } catch (error) {
        console.error('Failed to initialize pose landmarker:', error);
      }
    };

    if (isEnabled) {
      initializePoseLandmarker();
    }
  }, [isEnabled]);

  //   
  const detectPose = useCallback(
    async (videoElement: HTMLVideoElement) => {
      if (!poseLandmarkerRef.current || !isEnabled || isDetecting) return;

      const currentTime = videoElement.currentTime;
      if (currentTime === lastVideoTimeRef.current) return;

      lastVideoTimeRef.current = currentTime;
      setIsDetecting(true);

      try {
        const results = poseLandmarkerRef.current.detectForVideo(
          videoElement,
          performance.now(),
        );

        if (results.landmarks && results.landmarks.length > 0) {
          // 13     (MediaPipe Pose 33  )
          const keyLandmarks = extractKeyLandmarks(results.landmarks[0]);

          // World landmarks  (PI  )
          let worldLandmarks: PoseLandmark[] = [];
          if (results.worldLandmarks && results.worldLandmarks.length > 0) {
            worldLandmarks = extractKeyLandmarks(results.worldLandmarks[0]);
          } else {
            // World landmarks  2D  3D  
            worldLandmarks = keyLandmarks.map((landmark) => ({
              ...landmark,
              z: landmark.z || 0, // z  0 
            }));
            console.log(
              'World landmarks  - 2D  3D :',
              worldLandmarks.length,
            );
          }

          onPoseDetected?.(keyLandmarks, worldLandmarks);
        }
      } catch (error) {
        console.error('Pose detection error:', error);
      } finally {
        setIsDetecting(false);
      }
    },
    [isEnabled, isDetecting, onPoseDetected],
  );

  //    ( +  )
  const extractKeyLandmarks = (landmarks: PoseLandmark[]): PoseLandmark[] => {
    // MediaPipe Pose 33    
    const keyIndices = [
      //   (11)
      0, // NOSE
      1, // LEFT_EYE_INNER
      2, // LEFT_EYE
      3, // LEFT_EYE_OUTER
      4, // RIGHT_EYE_INNER
      5, // RIGHT_EYE
      6, // RIGHT_EYE_OUTER
      7, // LEFT_EAR
      8, // RIGHT_EAR
      9, // MOUTH_LEFT
      10, // MOUTH_RIGHT
      //   (2)
      11, // LEFT_SHOULDER
      12, // RIGHT_SHOULDER
    ];

    return keyIndices.map((index) => {
      const landmark = landmarks[index];
      if (landmark) {
        //     
        return {
          ...landmark,
          visibility: Math.max(landmark.visibility || 0, 0.1),
        };
      }
      return { x: 0, y: 0, z: 0, visibility: 0 };
    });
  };

  //     
  useEffect(() => {
    if (!isInitialized || !videoRef.current || !isEnabled) return;

    const getVideoElement = () => {
      // Webcam  video  
      const ref = videoRef.current;
      if (!ref) return null;
      // WebcamRef  
      if ('video' in ref) {
        return ref.video || null;
      }
      // Webcam   - video    
      return (
        (ref as unknown as { video?: HTMLVideoElement | null })?.video || null
      );
    };

    const interval = setInterval(() => {
      const videoElement = getVideoElement();
      if (videoElement && videoElement.readyState >= 2) {
        // HAVE_CURRENT_DATA
        detectPose(videoElement);
      }
    }, 50); // 20fps  (  )

    return () => clearInterval(interval);
  }, [isInitialized, videoRef, isEnabled, detectPose]);

  return null; //   UI  
};

export default PoseDetection;
